import React, { Component } from 'react'
import Nav from './NavBar'
import MobiNav from './mobileNav'
import M from "materialize-css";
import {Parallax, ParallaxLayer} from 'react-spring/renderprops-addons'
export default class Explore extends Component {

	componentDidMount() {
        // Auto initialize all the things!
        M.AutoInit();
    }
    
	
    render() {
        return (
            <div><div className="hide-on-med-and-down">
                <Nav></Nav>
				</div>
				<div className="mobile hide-on-med-and-up">
  <MobiNav/>
  </div>
                <main>
	<div className="menu">

		<div className="panel">
			<label className="text" for="toggle">Explore</label>
			<label className="icon fas fa-cog" for="toggle"></label>
		</div>

		<input type="checkbox" id="toggle"/>

		<div className="dropdown">
			<div className="arrow"></div>

			<a href="#" className="row">
				<div className="text">Bloging Essentials</div>
				<i className="icon fas fa-user"></i>
			</a>
			<a href="#" className="row">
				<div className="text">Places to visit in Himachal</div>
				<i className="icon far fa-chart-bar"></i>
			</a>
			<a href="#" className="row">
				<div className="text">Travel Music </div>
				<i className="icon fas fa-upload"></i>
			</a>
			<a href="#" className="row">
				<div className="text">Tracking Essentials</div>
				<i className="icon fas fa-pencil-alt"></i>
			</a>
		</div>

	</div>
</main><div>

	
</div>
            </div>
        )
    }
}
