import React, { Component } from 'react'
import {Link} from 'react-router-dom';
import '../CssFiles/navbar.css'
import M from "materialize-css";
import {Alert} from 'reactstrap'; 
export default class NavBar extends Component {
 
constructor(){
  super();
  this.state={
    searched_data:''
  }
  this.handlesearch=this.handlesearch.bind(this);
}
 

  handlesearch(e){
    
    this.setState({[e.target.name]:[e.target.value]})

    let search=this.state.searched_data;
    if(search=="suraj"){
      alert('name of developer')
    }
    else if(search==' '){
      alert('enter something')
    }
    else
    alert('Sorry, no results found')

  }
  render(){
      return (
        <div>
        <nav className="nav-wrapper grey darken-3 ">
     <div className="container"><a className="brand-logo" href="">Train For Life</a></div>
  
     <div className="heartrate"></div>
<ul className="right hide-on-med-and-down">
<li>
<form  className="form">
  <input type="search" className="input" placeholder="search"/>
 <i className="search ifa fa fa-search"><button className="ifa hide" name="searchname" onClick={(e)=>this.handlesearch(e)}></button></i> 
</form>
</li>
<li>  <Link to='/'>Home</Link></li>
    <li><Link to='/travel'>Travel</Link></li>
  <li>  <Link to='/health'>Health</Link></li>
    <li><Link to='/explore'>Explore</Link></li>
   
</ul>

<ul className="sidenav grey lighten-2" id="mobile-menu">
          <li><a href="#services">Services</a></li>
          <li><a href="#123">Programs</a></li>
          <li><a href="#contact">Contact</a></li>
      </ul>
      
    </nav> 
    
        </div>
    )
}
    }
   


